<template>
<div>
  <Table :columns="columns1" :data="listJson"></Table>
</div>
</template>

<script>
export default {
  name: '',
  props: {
    list: {
      type: Array,
      default: () => [{ name: '11' }] // 如果父组件没有传就是默认值
    }
  },
  data () {
    return {
      listJson: [],
      columns1: [{ title: 'name', key: 'name' }]
    }
  },
  mounted () {
    this.listJson = JSON.parse(JSON.stringify(this.list))
  }
}
</script>

