<template>
  <div class="about">
    <h1>mapState, mapMutations, mapActions, mapGetters</h1>
    <div>
      mapState--->{{mapCount}} |module-->| {{mapAstr}}
      <br>
      mapGetter--->{{getterCount}} ---对象重命名-{{aaa}}
      <br>
       mapMutations--><button @click='addCount(10)'>mapMutations+++</button>{{mapCount}}
       mapMutations--><button @click='mapreduceCount(10)'>mapMutations---</button>
       <br>
       mapActions--><button @click='addCount(10)'>mapActions+++</button>
       <br>
       module-->  <button @click='strAdd(10)'>module</button> {{mapAstr}}
       module-- namespaced:true>  <button @click='consoleStr()'>module</button> {{mapAstr}}
    </div>
  </div>
</template>
<script>
import { mapState, mapGetters, mapMutations, mapActions } from 'vuex'
export default {
  data () {
    return {
      num: 0
    }
  },
  computed: {
    ...mapState({
      mapCount: 'count',
      mapAstr: state => state.a.str
    }),
    ...mapGetters(['getterCount']),
    ...mapGetters({ aaa: 'getterCount' })
  },
  watch: {
    /*  num (n, o) {
      console.log(n, o)
    } */
    // num: {
    //   handler (n, o) {
    //     console.log(n, o)
    //   },
    //   deep: true
    // }
  },
  methods: {
    ...mapMutations([
      'addCount'
    ]),
    ...mapMutations({
      mapreduceCount: 'reduceCount'
    }),
    ...mapActions([
      'addCount'
    ]),
    ...mapMutations([
      'strAdd'
    ]),
    consoleStr () {
      this.$store.commit('b/handleStr')
    }
  }
}
</script>
