<template>
<div>
  <span @click="color=!color" :style="`color:${color? 'red':''}`">颜色切换</span>
  |||
  <span @click="objColor=!objColor" :style="{color:objColor?'yellow':''}">颜色切换</span>
  <br>
  改变icon
  <Icon @click="icon=!icon" :type="`${icon ?'md-arrow-round-up':'md-arrow-round-down'}`" size="24" />
<br>
<Button type="primary" @click='handleGet'>请求</Button>
<list-child v-if='list.length' :list='list'></list-child>
</div>
</template>

<script>
import axios from 'axios'
import ListChild from '@/components/listChild.vue'
export default {
  name: '',
  components: {
    ListChild
  },
  data () {
    return {
      color: false,
      objColor: false,
      icon: false,
      list: []
    }
  },
  methods: {
    handleGet () {
      // 'http://www.liulongbin.top:3005/api/addproduct'
      axios.get('http://www.liulongbin.top:3005/api/getprodlist')
        .then(res => {
          this.list = res.data.message
        })
    }
  }
}
</script>

